'use client';
import { Listbox } from '@headlessui/react';
import clsx from 'clsx';
import React, { forwardRef, useState } from 'react';
import { twMerge } from 'tailwind-merge';
import { getDistanceToBottom } from '../../utils/getDistanceToBottom';
import { FunctionalIcon } from '../icon/Icon';
import { DropDownItem, DropDownProps } from './drop-down.interface';

const MENU_HEIGHT = 268;

const getOptionItemClasses = (hovered: boolean, selected) => {
  return clsx(
    'zep-flex zep-w-full zep-items-center zep-justify-between zep-bg-background-light zep-px-1 zep-py-0.75',
    { 'zep-bg-greyscale-200': hovered },
    { 'zep-bg-greyscale-300': selected },
    { 'zep-pr-3': selected },
  );
};

export const DropDown: React.FC<DropDownProps> = forwardRef<HTMLElement, DropDownProps>(
  (
    {
      disabled = false,
      items,
      message,
      label,
      placeholder,
      required,
      notRequiredHint = '(optional)',
      readOnly = false,
      state = 'default',
      ...props
    },
    ref,
  ) => {
    const [selectedValue, setSelectedValue] = useState<DropDownItem | null>(null);
    const buttonRef = React.useRef<HTMLButtonElement>(null);
    const hasError = state === 'error';
    const hasInfo = state === 'info';
    const disabledOrReadonly = disabled || readOnly;
    const iconColor = hasError ? 'zep-text-error' : hasInfo ? 'zep-text-info' : '';
    const onChange = (value) => {
      props?.onChange && props?.onChange(value);
      setSelectedValue(value);
    };

    return (
      <div
        className={twMerge(
          clsx(
            'zep-relative',
            'zep-inline-block',
            'zep-text-left',
            'zep-w-full',
            {
              'zep-cursor-not-allowed zep-opacity-disabled': disabled,
            },
            props.className,
          ),
        )}
        data-testid="zep-drop-down"
      >
        <Listbox
          disabled={disabledOrReadonly}
          {...props}
          onChange={onChange}
          value={props?.value || selectedValue}
          ref={ref}
        >
          {({ open }) => {
            const distanceToBottom = getDistanceToBottom(buttonRef.current);
            const openOnTop = distanceToBottom < MENU_HEIGHT;
            const showPlaceholder = readOnly ? undefined : placeholder;
            const displayValue = props?.value?.name || selectedValue?.name || showPlaceholder;
            const color = disabled
              ? 'zep-text-opacity-disabled'
              : !props?.value?.name && !selectedValue?.name && !readOnly
              ? 'zep-text-typography-dark-100/70'
              : 'zep-text-typography-dark-100';

            // input
            const buttonClasses = clsx(
              'zep-bg-background-light',
              'zep-inline-flex',
              'zep-justify-between',
              'zep-rounded-4',
              'zep-border-solid',
              'zep-pl-1',
              'zep-pr-0.75',
              'zep-py-0.75',
              'zep-font-roboto',
              'zep-w-full',
              'zep-outline-none',
              hasError ? 'zep-border-2 zep-border-error' : 'zep-border-1 zep-border-neutral-dark-default',
              color,
              { 'zep-bg-background-medium': readOnly },
              { 'zep-ring': open },
              { 'zep-active:zep-ring': !disabledOrReadonly },
              { 'zep-ring-offset-1': !disabledOrReadonly },
              { 'focus-visible:zep-ring': !disabledOrReadonly },
              { 'zep-ring-focus': !disabledOrReadonly },
              { 'zep-cursor-not-allowed': disabledOrReadonly },
            );

            const optionContainerClasses = clsx(
              'zep-absolute',
              'zep-shadow-sm',
              'zep-py-0.25',
              'zep-z-10',
              'zep-w-full',
              'zep-max-h-[268px]',
              'zep-origin-top-right',
              'zep-rounded-4',
              'zep-bg-background-light',
              'focus:zep-outline-none',
              'zep-overflow-hidden',
              'zep-overflow-y-auto',
              openOnTop ? 'zep-mb-[-25px]' : 'zep-mt-0.25',
              { 'zep-bottom-full': openOnTop },
            );

            const iconClasses = clsx(
              disabledOrReadonly ? 'zep-text-opacity-disabled' : 'zep-text-typography-dark-100',
              'zep-shrink-0',
              'zep-transition-transform',
              'zep-duration-300',
              {
                'zep-rotate-180': open,
              },
            );

            return (
              <>
                {label && (
                  <Listbox.Label
                    className={clsx('zep-block', 'zep-mb-0.5', {
                      'zep-text-opacity-disabled': disabled,
                    })}
                  >
                    {label}
                    {!required && <span className="zep-ml-0.25">{notRequiredHint}</span>}
                  </Listbox.Label>
                )}

                <Listbox.Button
                  className={buttonClasses}
                  ref={buttonRef}
                  title={displayValue}
                  data-testid="zep-drop-down-button"
                >
                  <>
                    <span className="zep-truncate">{displayValue}</span>
                    <FunctionalIcon className={iconClasses} name="chevron-mini-down" role="img" size={24} />
                  </>
                </Listbox.Button>
                <Listbox.Options className={optionContainerClasses} data-testid="zep-drop-down-menu">
                  {items.map((item, i) => (
                    <Listbox.Option
                      key={item.id}
                      value={item}
                      title={item.name}
                      className={({ active, selected }) => getOptionItemClasses(active, selected)}
                    >
                      {({ selected }) => {
                        return (
                          <>
                            <span className="zep-truncate">{item.name}</span>
                            {selected && (
                              <FunctionalIcon name="check" role="img" size={24} className="zep-absolute zep-right-1" />
                            )}
                          </>
                        );
                      }}
                    </Listbox.Option>
                  ))}
                </Listbox.Options>
              </>
            );
          }}
        </Listbox>
        {message && (
          <div
            className={clsx(
              'zep-mt-0.25',
              { 'zep-text-typography-dark-100': !hasError && !hasInfo },
              'zep-text-0.875',
              'zep-flex',
              iconColor,
            )}
          >
            {state !== 'default' && (
              <FunctionalIcon
                name={hasError ? 'warning-triangle-filled' : 'info-circle-filled'}
                role="img"
                size={24}
                className="zep-mr-0.5 zep-shrink-0"
              />
            )}
            <span className="zep-mt-0.125">{message}</span>
          </div>
        )}
      </div>
    );
  },
);
